@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -msbox-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
    width: 9px;
}
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background: #a4b1b0;
}

.App{
  display: flex;
  min-height: 93.1vh;
  flex-direction: column; 
}

.content { 
  flex: 1 1; 
}

.footer {   
  flex-shrink: 0; 
  height: 50px;
  text-align: center;
}

.my-threads li{
  list-style: none;
}

.my-threads li:hover{
  color: rgb(150, 150, 150);
  cursor: pointer;
}
.my-threads li:nth-of-type(2) {
  background-color: rgb(255, 157, 189)t;
}

.found_user:hover{
  background-color: #ffeec8;
}

.subject-name{
  color:rgb(208, 255, 0);
}
.subject-name:hover{
  cursor: pointer;
  color: rgb(250, 250, 250);
}

.jumbotron{
  background: #ECE9E6; 
  background: linear-gradient(to top, #FFFFFF, #ECE9E6);
}

.jumbotron h1{
  text-shadow:  0px 2px 10px rgba(0,0,0,0.2),
                0px 3px 10px rgba(0,0,0,0.1),
                0px 10px 10px rgba(0,0,0,0.05);
}
